.Order {
  /*sirina jednog ordera*/
  width: 80%;

  border: 1px solid black;
  box-shadow: 0 2px 3px #ccc;
  padding: 20px;
  margin: 70px auto;
  /*box-sizing: border-box;*/
}

.IngredientOutput {
  border: 1px solid #ccc;
  padding: 5px;
  text-transform: capitalize;
  display: inline-block;
  margin: 0 8px;
  display: block;
}

.IngredintsPadding {
  padding-bottom: 5%;
  display: block;
}

@media only screen and (min-width: 1200px) {
  .Order {
    font-size: 1.2em;
  }
  .IngredientOutput {
    display: inline-block;
  }
  .IngredintsPadding {
    padding-bottom: 1%;
    display: inline-block;
  }
}
