.Burger {
  width: 100%;
  margin: auto;
  height: 250px;

  /*Ovo ispod dolazi do izrazaja kada je u pitanju veliki hamburger sa puno sastojaka, treba testirati poslije*/
  overflow-y: scroll;

  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
}

.SastojciP {
  font-size: 0.8em;
}

@media (min-width: 150px) and (max-width: 340px) {
  .Burger {
    width: 180px;
    height: 140px;
  }
  .SastojciP {
    font-size: 0.6em;
  }
}

@media (min-width: 340px) and (max-width: 500px) {
  .Burger {
    width: 200px;
    height: 170px;
  }
  .SastojciP {
    font-size: 0.6em;
  }
}

@media (min-width: 500px) and (min-height: 400px) {
  .Burger {
    width: 350px;
    height: 343px;
  }
}

@media only screen and (min-width: 768px) {
  .Burger {
    width: 350px;
    height: 343px;
  }
  .SastojciP {
    font-size: 1.1em;
  }
}

@media only screen and (min-width: 1200px) {
  .Burger {
    width: 450px;
    height: 300px;
  }
}

@media only screen and (min-width: 1400px) {
  .Burger {
    width: 350px;
    height: 250px;
  }
  .SastojciP {
    font-size: 1.1em;
  }
}
