.BuildControls {
  width: 100%;
  height: 50%;
  background-color: #cf8f2e;

  display: flex;
  flex-flow: column;
  align-items: center;
  box-shadow: 0 2px 1px #ccc;
  margin: auto;
  padding: 10px 0;

  position: fixed;
  bottom: 0;
}

.TrenutnaCijena {
  font-size: 0.8em;
}

.OrderButton {
  background-color: #dad735;
  outline: none;
  cursor: pointer;
  border: 1px solid #966909;
  color: #966909;
  font-family: inherit;
  font-size: 0.8em;
  padding: 10px 20px;
  box-shadow: 2px 2px 2px #966909;
}

@media only screen and (min-width: 600px) {
  .OrderButton {
    font-size: 1em;
    padding: 15px 30px;
  }
}

.OrderButton:hover,
.OrderButton:active {
  background-color: #a0db41;
  border: 1px solid #966909;
  color: #966909;
}

.OrderButton:disabled {
  background-color: #c7c6c6;
  cursor: not-allowed;
  border: 1px solid #ccc;
  color: #888888;
}

.OrderButton:not(:disabled) {
  animation: enable 0.3s linear;
}

@keyframes enable {
  0% {
    transform: scale(1);
  }
  60% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

@media only screen and (min-width: 600px) {
  .BuildControls {
    height: 40%;
  }
  .TrenutnaCijena {
    font-size: 0.9em;
  }
}

@media only screen and (min-width: 1200px) {
  .OrderButton {
    font-size: 1em;
  }
  .BuildControls {
    height: 45%;
  }
}

@media only screen and (min-width: 1700px) {
  .OrderButton {
    font-size: 1.2em;
  }
  /*.BuildControls {
    position: fixed;
    bottom: 0;
    padding: 10px 0;
  }*/
  .TrenutnaCijena {
    font-size: 1em;
  }
  .BuildControls {
    height: 40%;
  }
}
